.experience-main {
    width: 100%;
}

.basic-experience {
    margin-left: 5%;
    margin-right: 5%;
}

.experience-heading-div {
    display: flex;
}

.experience-heading-div > * {
    flex: 1;
}

.experience-heading-img-div {
    align-items: center;
    justify-content: center;
}

.experience-heading-text-div {
    text-align: center;
}

.experience-heading-img-div > * {
    max-width: 100%;
    height: auto;
}

.experience-heading-text {
    font-size: 56px;
    font-weight: 400;
    font-family: "Google Sans Medium", sans-serif;
    line-height: 1.1;
    text-align: center;
    margin-top: 80px;
}

.experience-heading-sub-text {
    font-size: 30px;
    font-family: "Google Sans Regular", sans-serif;
    text-align: center;
    margin-bottom: 10px;
}

.experience-header-detail-text {
    font-size: 20px;
    line-height: 30px;
    margin-top: 0;
    margin-left: 20px;
    margin-right: 20px;
    font-family: "Google Sans Medium", sans-serif;
}

@media (max-width: 1380px) {
    .experience-heading-text {
        font-size: 50px;
        margin-top: 80px;
    }

    .experience-heading-sub-text {
        font-size: 25px;
    }
}

@media (max-width: 768px) {
    .experience-heading-text {
        font-size: 30px;
        margin-top: 20px;
    }

    .experience-heading-sub-text {
        font-size: 20px;
    }

    .experience-heading-div {
        flex-direction: column;
    }

    .experience-header-detail-text {
        font-size: 16px;
        margin-right: 0;
        line-height: normal;
        text-align: center;
    }
}
