#topButton {
    visibility: hidden;
    position: fixed;
    bottom: 1.2rem;
    right: 1.5rem;
    cursor: pointer;
    padding: 15px;
    outline: none;
    border-radius: 2rem;
    font-size: 1.5rem;
}

#topButton:hover {
    box-shadow: 0 0 5px #555;
    background-color: #ffffff;
}
