.main {
    width: 90%;
    padding: 20px 10px;
    margin: 4rem auto 0;
}

.greet-main {
    width: 90%;
    padding: 0 10px;
    margin: 0 auto 0;
}

.gretting-main {
    width: 90%;
    padding: 20px 10px;
    margin: 0 auto;
}

.greeting-main {
    display: flex;
}

.greeting-main > * {
    flex: 1;
    margin-bottom: 30px;
}

.button-greeting-div {
    display: flex;
    margin-top: 20px;
}

.greeting-text {
    margin-top: 80px;
    font-size: 70px;
    line-height: 1.1;
    font-family: "Google Sans Bold", sans-serif;
}

.greeting-nickname {
    font-size: 30px;
    font-family: "Google Sans Bold Italic", sans-serif;
}

.greeting-text-p {
    font-size: 40px;
    line-height: 40px;
    margin-right: 40px;
    font-family: "Google Sans Medium", sans-serif;
}

.greeting-image-div > * {
    max-width: 100%;
    height: auto;
    display: flex;
}

/* Media Query */
@media (max-width: 1380px) {
    .greeting-text {
        font-size: 50px;
    }

    .greeting-text-p {
        font-size: 20px;
    }
}

@media (max-width: 768px) {
    .button-greeting-div {
        justify-content: space-around;
    }

    .greeting-text {
        font-size: 30px;
        margin-top: 0;
        text-align: center;
    }

    .greeting-nickname {
        font-size: 25px;
        text-align: center;
    }

    .greeting-text-p {
        font-size: 16px;
        margin-right: 0;
        line-height: normal;
        text-align: center;
    }

    .greeting-main {
        display: block;
    }

    .portfolio-repo-btn-div {
        width: 100%;
        display: flex;
        justify-content: center;
    }
}
