.education-main {
    width: 100%;
}

.basic-education {
    margin-left: 5%;
    margin-right: 5%;
}

.heading-div {
    display: flex;
}

.heading-div > * {
    flex: 1;
}

.heading-img-div {
    align-items: center;
    justify-content: center;
}

.heading-text-div {
    text-align: center;
}

.heading-img-div > * {
    max-width: 100%;
    height: auto;
}

.heading-text {
    font-size: 56px;
    font-weight: 400;
    font-family: "Google Sans Medium", sans-serif;
    line-height: 1.1;
    text-align: center;
    margin-top: 100px;
}

.heading-sub-text {
    font-size: 30px;
    font-family: "Google Sans Regular", sans-serif;
    text-align: center;
}

@media (max-width: 1380px) {
    .heading-text {
        font-size: 50px;
        margin-top: 80px;
    }

    .heading-sub-text {
        font-size: 25px;
    }
}

@media (max-width: 768px) {
    .heading-text {
        font-size: 30px;
        margin-top: 20px;
    }

    .heading-sub-text {
        font-size: 20px;
    }

    .heading-div {
        flex-direction: column;
    }
}

.educations-header {
    font-size: 40px;
    line-height: 1.1;
    font-family: "Google Sans Regular", sans-serif;
    text-align: center;
}

@media (max-width: 1380px) {
    .educations-header {
        font-size: 35px;
    }
}

@media (max-width: 768px) {
    .educations-header {
        font-size: 30px;
    }
}
