.social-media-div {
    font-size: 2em;
    margin-bottom: 20px;
}

.icon-button {
    margin-bottom: 10px;
}

.icon-button i {
    color: white;
    border-radius: 2.6rem;
    cursor: pointer;
    display: inline-block;
    font-size: 1.3rem;
    height: 2.6rem;
    line-height: 2.6rem;
    position: relative;
    text-align: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 2.6rem;
    margin: 0 5px 10px;
}

/*Hover Transition*/
i:hover {
    transition: 0.3s ease-in;
}

/* Media Query */
@media (max-width: 768px) {
    .social-media-div {
        text-align: center;
    }
}
